<template>
  <div id="test">
    <!-- <div style="width:200px;height:200px;border:1px solid"></div>
    <div
      style="height: 432px"
      id="zhankaiStyle"
      class="bottom-form"
      ref="kongtiao"
      @mousedown="mouseDownHandleelse($event)"
      @mouseup="mouseUpHandleelse($event)"
    ></div>-->
    <vue-drag-resize :w="200" :h="200" v-on:resizing="resize" v-on:dragging="resize">
      <!-- 图片缩放 -->
      将这个div的宽高动态设置==VueDrangResize的宽高，这样可实时完成缩放
    </vue-drag-resize>
  </div>
</template>

<script>
import Cookies from "js-cookie";
export default {
  data() {
    return {
      moveDataelse: {
        x: null,
        y: null,
        TopDong: null
      },
      vw: 0,
      vh: 0,
      top: 0,
      left: 0
    };
  },
  created() {
    this.vw = 200 + "px";
    this.vh = 200 + "px";
  },
  computed: {
    // 检测定位top的变化
    TopDongChange() {
      return this.moveDataelse.TopDong;
    }
  },
  mounted() {
    // 给元素一个起始的高度值，并存在cookie中
    Cookies.set("chazhiValue", 432);
  },
  watch: {
    // 定位top的变化值，就是元素高度的变化值，通过原始值和元素top定位的变化值最终得到的就是height高度值
    TopDongChange(newVal, oldVal) {
      console.log(newVal, oldVal);
      var reduce;
      // 因为oldVal起始值为null，所以先排除掉
      if (!oldVal && typeof oldVal === "object") {
        reduce = 0;
      } else {
        reduce = oldVal - newVal;
      }
      document.getElementById("zhankaiStyle").style.height =
        Number(Cookies.get("chazhiValue")) + reduce + "px";
      Cookies.set("chazhiValue", Number(Cookies.get("chazhiValue")) + reduce);
    }
  },
  methods: {
    // 鼠标按下
    mouseDownHandleelse(event) {
      this.moveDataelse.y = event.pageY - this.$refs.kongtiao.offsetTop;
      event.currentTarget.style.cursor = "move";
      window.onmousemove = this.mouseMoveHandleelse;
    },
    // 鼠标移动
    mouseMoveHandleelse(event) {
      this.moveDataelse.TopDong = event.pageY - this.moveDataelse.y;
    },
    // 鼠标松开
    mouseUpHandleelse(event) {
      window.onmousemove = null;
      event.currentTarget.style.cursor = "move";
    },
    resize(newRect) {
      this.vw = newRect.width;
      this.vh = newRect.height;
      this.top = newRect.top;
      this.left = newRect.left;
    }
  }
};
</script>
<style lang="less" scoped>
.form-all-style {
  width: 100%;
  height: 100%;
}

.bottom-form {
  width: calc(100% - 120px);
  position: absolute;
  bottom: 0px;
  height: 432px;
  max-height: 600px;
  padding-top: 30px;
  background-color: saddlebrown;
}
</style>
